import axios from "axios";

export async function logingIn(formData: {}) {
  const data = await axios.post("https://shope-back-3.onrender.com/login/", formData)
  return data;
}

export async function register(formData: {}) {
  return await axios.post("https://shope-back-3.onrender.com/register/", formData)
}
