import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  buyProdAsync,
  deleteAsync,
  loadDataAsync,
  selectProducts,
  updProdAsync,
} from "./prodSlice";
import { selectAdmin } from "../login/loginSlice";
import {
  Button,
  Card,
  CardContent,
  CardActions,
  Typography,
  Grid,
  CircularProgress,
} from "@mui/material";

interface ProdLstProps {
  myFlag: boolean;
  setFlag: React.Dispatch<React.SetStateAction<boolean>>;
}

export function ProdLst(props: ProdLstProps): JSX.Element {
  const [newdesc, setNewdesc] = useState<string>("");
  const [newprice, setNewprice] = useState<number>(0);
  const [newImg, setNewImg] = useState<File | null>(null);
  const admin: Boolean = useSelector(selectAdmin);
  const products = useSelector(selectProducts);
  const dispatch: any = useDispatch();

  useEffect(() => {
    dispatch(loadDataAsync());
  }, [props.myFlag, dispatch]);

  const handleDel = async (prodID: number): Promise<void> => {
    await dispatch(deleteAsync(prodID));
    props.setFlag((prevFlag) => !prevFlag);
  };

  const handleUpd = async (prodID: number): Promise<void> => {
    const formData = new FormData();
    formData.append("desc", newdesc);
    formData.append("price", newprice.toString());
    if (newImg) {
      formData.append("img", newImg);
    }

    await dispatch(
      updProdAsync({
        prodID: prodID,
        formData: formData,
      })
    );
    props.setFlag((prevFlag) => !prevFlag);
  };

  const handleBuy = async (prodID: number): Promise<void> => {
    await dispatch(buyProdAsync(prodID));
    props.setFlag((prevFlag) => !prevFlag);
  };

  return (
    <div style={{maxHeight: '450px', overflowY: 'auto'}}>
      {products.length === 0 ? (
        <CircularProgress style={{ margin: "50px auto", display: "block"}} />
      ) : (
        <Grid container spacing={3} mt={3}>
          {products.map((product) => (
            <Grid item key={product.id} xs={12} sm={6} md={3}>
              <Card>
                <img
                  src={`https://shope-back-3.onrender.com${product.img}`}
                  alt={`Product ${product.id}`}
                  style={{
                    width: "100%",
                    height: "150px",
                    objectFit: "cover",
                  }}
                />
                <CardContent>
                  <Typography variant="h6" component="div">
                    Description: {product.desc}
                  </Typography>
                  <Typography variant="subtitle1" color="textSecondary">
                    Price: {product.price}
                  </Typography>
                </CardContent>
                <CardActions>
                  {admin && (
                    <Button
                      variant="outlined"
                      color="error"
                      onClick={() => handleDel(product.id)}
                    >
                      Delete
                    </Button>
                  )}
                  {admin && (
                    <Button
                      variant="outlined"
                      color="warning"
                      onClick={() => handleUpd(product.id)}
                    >
                      Update
                    </Button>
                  )}
                  <Button
                    variant="outlined"
                    color="info"
                    onClick={() => handleBuy(product.id)}
                  >
                    Buy
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      )}
    </div>
  );
}
