import axios from "axios";

export async function getCart() {
  const myToken = sessionStorage.getItem("access");
  return await axios.get(`https://shope-back-3.onrender.com/getCart/`, {
    headers: {
      "Content-Type": "application/json",
      Authorization: "Bearer " + myToken,
    },
  });
}

export async function unBuy(prodID: Number) {
  const myToken = sessionStorage.getItem("access");
  return await axios.delete(`https://shope-back-3.onrender.com/unbuy/${prodID}`, {
    headers: {
      "Content-Type": "application/json",
      Authorization: "Bearer " + myToken,
    },
  });
}

export async function checkOut() {
  const myToken = sessionStorage.getItem("access");
  return await axios.get(`https://shope-back-3.onrender.com/checkOut/`, {
    headers: {
      "Content-Type": "application/json",
      Authorization: "Bearer " + myToken,
    },
  });
}

export async function calcTotal() {
  const myToken = sessionStorage.getItem("access");
  return await axios.post(
    `https://shope-back-3.onrender.com/calcTotal/`,
    {},
    {
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + myToken,
      },
    }
  );
}
