import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { logingIn, register } from './loginAPI';

export const parseJwt = (token: any) => {
  var base64Url = token.split(".")[1];
  var base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
  var jsonPayload = decodeURIComponent(
    window
      .atob(base64)
      .split("")
      .map(function (c) {
        return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
      })
      .join("")
  );
  return JSON.parse(jsonPayload);
};

const admin_check= () => {
  try {
    const token = sessionStorage.getItem('access')
    return parseJwt(token).is_admin
  } catch {
    return false
  }
}

const username_check = () => {
  try {
    const token = sessionStorage.getItem('access')
    return parseJwt(token).username
  } catch {
    return ""
  }
}

export interface LoginState {
  loggedIn: Boolean
  status: 'idle' | 'loading' | 'failed';
  is_admin: Boolean
  username: String
}

const initialState: LoginState = {
  loggedIn: sessionStorage.getItem("access") ? true : false,
  status: "idle",
  is_admin: admin_check(),
  username: username_check()
};


export const loginAsync = createAsyncThunk(
  "login/logingIn",
  async (formData: {}) => {
    const response = await logingIn(formData);
    return response.data;
  }
);

export const registerAsync = createAsyncThunk(
  "login/register",
  async (formData: {}) => {
    const response = await register(formData);
    return response.data;
  }
);

export const loginSlice = createSlice({
  name: "login",
  initialState,
  reducers: {
    logOut: (state) => {
      sessionStorage.clear()
      state.loggedIn = false;
      window.location.href = './'
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(loginAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(loginAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedIn = true;
        sessionStorage.setItem("access", action.payload.access);
        state.is_admin = parseJwt(sessionStorage.getItem("access")).is_admin;
      });
  },
});

export const { logOut } = loginSlice.actions;
export const selectCount = (state: { login: { loggedIn: Boolean; }; }) => state.login.loggedIn;
export const selectAdmin = (state: { login: { is_admin: Boolean; }; }) => state.login.is_admin;
export const username = (state: {login: { username: String};}) => state.login.username
export default loginSlice.reducer;
