import React, { Dispatch, SetStateAction, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  calcTotalAsync,
  getCartAsync,
  selectCart,
  selectTotal,
  unBuyAsync,
} from "./cartSlice";
import Pay from "./Paypal";
import {
  Button,
  Card,
  CardContent,
  Collapse,
  Typography,
} from "@mui/material";

export function Cart(props: {
  myFlag: boolean;
  setFlag: Dispatch<SetStateAction<boolean>>;
}) {
  const MyCart = useSelector(selectCart);
  const dispatch: any = useDispatch();
  const total: number = useSelector(selectTotal);

  useEffect(() => {
    dispatch(getCartAsync());
  }, [dispatch, props.myFlag]);

  useEffect(() => {
    dispatch(calcTotalAsync());
  }, [MyCart, total, dispatch]);

  const handleDelete = async (prodID: Number) => {
    console.log(prodID);
    await dispatch(unBuyAsync(prodID));
    props.setFlag(!props.myFlag);
  };

  return (
    <div style={{ margin: "2%" }}>
      <Typography variant="h6" component="div" gutterBottom>
        <Button
          variant="contained"
          color="success"
          size="small"
          data-bs-toggle="collapse"
          data-bs-target="#collapseExample"
          aria-expanded="false"
          aria-controls="collapseExample"
          style={{ marginTop: "6%" }}
        >
          My Cart: {MyCart.length}
        </Button>
      </Typography>
      <Collapse in={true} timeout="auto" unmountOnExit>
        <Card
          sx={{
            maxHeight: "450px",
            overflowX: "auto",
            border: "1px solid",
            borderRadius: "18px",
          }}
        >
          <CardContent>
            <Typography variant="h5" gutterBottom>
              {MyCart.length} products
            </Typography>
            <Typography variant="h4" gutterBottom>
              Total 2 Pay: {total}
            </Typography>
            {total > 0 ? (
              <Pay flag={props.myFlag} setFlag={props.setFlag} />
            ) : (
              <div></div>
            )}
            {MyCart.length > 0 ? (
              MyCart.map(
                (product: {
                  desc: string;
                  price: number;
                  id: number;
                  quantity: number;
                }) => {
                  console.log(product);
                  return (
                    <Card
                      key={product.id}
                      sx={{
                        border: "1px solid",
                        borderRadius: "8px",
                        margin: "10px",
                        padding: "5px",
                      }}
                    >
                      <CardContent>
                        <Typography variant="body1" gutterBottom>
                          Description: {product.desc}
                          <br />
                          Price: ${product.price}
                          <br />
                          Quantity: {product.quantity}
                        </Typography>
                        <Button
                          variant="contained"
                          color="error"
                          onClick={() => handleDelete(product.id)}
                        >
                          Del
                        </Button>
                      </CardContent>
                    </Card>
                  );
                }
              )
            ) : (
              <Typography variant="body1">No products in the cart</Typography>
            )}
          </CardContent>
        </Card>
      </Collapse>
    </div>
  );
}
