import React, { useState } from "react";
import { useSelector } from "react-redux";
import { selectAdmin, selectCount } from "../login/loginSlice";
import {
  Grid,
  Typography,
  Accordion,
  AccordionSummary,
  AccordionDetails,
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { ProdLst } from "./ProdLst";
import { ProdAdd } from "./ProdAdd";
import { Cart } from "../cart/Cart";


const Prod = () => {
  const [loadFlag, setLoadFlag] = useState(true);
  const admin = useSelector(selectAdmin);
  const logged = useSelector(selectCount);

  return (
    <div style={{ display: 'flex' }}>
      <Grid container>
        <Grid item xs={2}>
          <Accordion>
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>Cart</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Cart myFlag={loadFlag} setFlag={setLoadFlag} />
            </AccordionDetails>
          </Accordion>
        </Grid>
        <Grid item xs={9}>
          {admin ? (
            <div>
              <Typography variant="h4" component="div" gutterBottom>
                Welcome to my store
              </Typography>
              <Accordion>
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                  <Typography>Add Product</Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <ProdAdd myFlag={loadFlag} setFlag={setLoadFlag} />
                </AccordionDetails>
              </Accordion>
            </div>
          ) : (
            <Typography variant="h2" component="div" gutterBottom>
              Welcome to my store
            </Typography>
          )}
          {logged ? (
            <ProdLst myFlag={loadFlag} setFlag={setLoadFlag} />
          ) : (
            <Typography variant="h3" component="div" gutterBottom>
              Please Log In
            </Typography>
          )}
        </Grid>
      </Grid>
    </div>
  );
};

export default Prod;