import React, { useState, useEffect, Dispatch, SetStateAction, ChangeEvent } from "react";
import { useDispatch } from "react-redux";
import { Button, TextField, Typography, Container, Grid, Paper } from "@mui/material";
import { addProdAsync, loadDataAsync } from "./prodSlice";

interface ProdAddProps {
  myFlag: boolean;
  setFlag: Dispatch<SetStateAction<boolean>>;
}

export function ProdAdd(props: ProdAddProps): JSX.Element {
  const [newdesc, setNewdesc] = useState<string>("");
  const [newprice, setNewprice] = useState<number>(0);
  const [newImg, setNewImg] = useState<File | null>(null);
  const dispatch: any = useDispatch();

  useEffect(() => {
    dispatch(loadDataAsync());
  }, [props.myFlag, dispatch]);

  const handleAdd = async (): Promise<void> => {
    const formData = new FormData();
    formData.append("desc", newdesc);
    formData.append("price", String(newprice));
    if (newImg) {
      formData.append("img", newImg);
    }
    await dispatch(addProdAsync(formData));
    props.setFlag(!props.myFlag);
  };

  const handleDescChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setNewdesc(e.target.value);
  };

  const handlePriceChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setNewprice(Number(e.target.value));
  };

  const handleImgChange = (e: ChangeEvent<HTMLInputElement>): void => {
    if (e.target.files && e.target.files.length > 0) {
      setNewImg(e.target.files[0]);
    }
  };

  return (
    <Container component="main" maxWidth="sm">
      <Paper elevation={3} sx={{ padding: 3, display: "flex", flexDirection: "column", alignItems: "center" }}>
        <Typography component="h1" variant="h5">
          Products
        </Typography>
        <TextField
          margin="normal"
          fullWidth
          label="Description"
          placeholder="Default description"
          value={newdesc}
          onChange={handleDescChange}
        />
        <TextField
          margin="normal"
          fullWidth
          label="Price"
          type="number"
          placeholder="1"
          value={newprice}
          onChange={handlePriceChange}
        />
        <input
          type="file"
          id="productImg"
          style={{ display: "none" }}
          onChange={handleImgChange}
        />
        <label htmlFor="productImg">
          <Button variant="contained" component="span" sx={{ marginTop: 2 }}>
            Choose File
          </Button>
        </label>
        <Button
          type="button"
          variant="contained"
          color="primary"
          onClick={handleAdd}
          sx={{ marginTop: 3 }}
        >
          Add Product
        </Button>
      </Paper>
    </Container>
  );
}
