import React, { useEffect, useState } from "react";
import { Link, Outlet } from "react-router-dom";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import { Button } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { logOut, selectCount, username } from "./features/login/loginSlice";

const MyHeader = () => {
  const is_logg: Boolean = useSelector(selectCount);
  const myUsername: String = useSelector(username);
  const [message, setMessage] = useState("");
  const dispatch = useDispatch();

  useEffect(() => {
    setMessage(
      is_logg
        ? `Welcome ${myUsername}, happy shoping`
        : "welcome, please log in"
    );
  }, [is_logg]);

  return (
    <div>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" style={{ flexGrow: 0 }}>
            {message}
          </Typography>
          <Typography variant="h6" style={{ flexGrow: 1 }}></Typography>
          <Link to="/" style={{ textDecoration: "none", color: "inherit" }}>
            <Button color="inherit">Home</Button>
          </Link>
          {is_logg && (
            <Link
              to="/product"
              style={{ textDecoration: "none", color: "inherit" }}
            >
              <Button color="inherit">Product</Button>
            </Link>
          )}
          <Button
            variant="text"
            sx={{ color: "white" }}
            onClick={() => dispatch(logOut())}
          >
            Log Out
          </Button>
        </Toolbar>
      </AppBar>
      <Outlet />
    </div>
  );
};

export default MyHeader;
