import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { addProd, updProd, loadData, deleteProd, buyProd } from "./prodAPI";
import { logOut } from "../login/loginSlice";
import { RootState } from "../../app/store";

export interface ProdState {
  status: "idle" | "loading" | "failed";
  myProds: [{ id: number; price: number; desc: string; img: string }] | [];
}

const initialState: ProdState = {
  status: "idle",
  myProds: [],
};

export const loadDataAsync = createAsyncThunk("products/loadData", async () => {
  const response = await loadData();
  return response.data;
});

export const deleteAsync = createAsyncThunk(
  "products/deleteProd",
  async (prodID: Number) => {
    const response = await deleteProd(prodID);
    return response.data;
  }
);

export const addProdAsync = createAsyncThunk(
  "products/addProd",
  async (formData: {}) => {
    const response = await addProd(formData);
    return response.data;
  }
);

export const updProdAsync = createAsyncThunk(
  "products/updProd",
  async (myData: {prodID: Number, formData: {}}) => {
    const response = await updProd(myData.prodID, myData.formData);
    return response.data;
  }
);

export const buyProdAsync = createAsyncThunk(
  "products/buyProd",
  async (prodID: Number) => {
    const response = await buyProd(prodID);
    return response.data;
  }
);

export const prodSlice = createSlice({
  name: "prod",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(loadDataAsync.fulfilled, (state, action) => {
        state.myProds = action.payload;
        console.log(action.payload);
        
      })
      .addCase(loadDataAsync.rejected, (state, action) => {
        logOut()
        console.log("reject");
      })
      .addCase(buyProdAsync.rejected, (state, action) => {
        logOut()
      });
  },
});

export const selectProducts = (state: RootState) => state.prod.myProds
export default prodSlice.reducer;
