import {
  createAsyncThunk,
  createSlice,
  createSelector,
} from "@reduxjs/toolkit";
import { calcTotal, checkOut, getCart, unBuy } from "./cartAPI";
import { logOut } from "../login/loginSlice";
import { RootState } from "../../app/store";

export interface cartState {
  status: 'idle' | 'loading' | 'failed';
  // myCart: [[]] | [{desc: String, price: number, quantity: number, id: number}]
  myCart: [],
  total: number
}

const initialState: cartState = {
  status: "idle",
  myCart: [],
  total: 0
};

export const getCartAsync = createAsyncThunk("cart/getCart", async () => {
  const response = await getCart();
  return response.data;
});

export const unBuyAsync = createAsyncThunk("cart/unBuy", async (prodID: Number) => {
  const response = await unBuy(prodID);
  return response.data;
});

export const checkOutAsync = createAsyncThunk(
  "cart/checkOut",
  async () => {
    const response = await checkOut();
    return response.data;
  }
);

export const calcTotalAsync = createAsyncThunk(
  "cart/calcTotal",
  async () => {
    const response = await calcTotal();
    return response.data;
  }
);

export const cartSlice = createSlice({
  name: "cart",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(getCartAsync.fulfilled, (state, action) => {
        state.myCart = action.payload
      })
      .addCase(getCartAsync.rejected, (state) => {
        logOut()
      })
      .addCase(calcTotalAsync.fulfilled, (state, action) => {
        state.total = action.payload.total
      })
      .addCase(checkOutAsync.fulfilled, (state, action) => {
      })
  },
});

export const selectCart = (state: RootState) => (state.cart ? state.cart.myCart : []);
export const selectTotal = (state: RootState) => (state.cart.total)

export const memoizedSelectCart = createSelector(
  [selectCart],
  (myCart) => myCart
);

export default cartSlice.reducer;
