import React, { Dispatch, SetStateAction, useEffect, useState } from "react";
import { PayPalScriptProvider, PayPalButtons } from "@paypal/react-paypal-js";
import { useDispatch, useSelector } from "react-redux";
import { checkOut } from "./cartAPI";
import { getCartAsync, selectTotal } from "./cartSlice";

const Pay = (props: {flag: boolean, setFlag: Dispatch<SetStateAction<boolean>>}) => {
  const dispatch: any = useDispatch();
  const total = useSelector(selectTotal);

  const paypalOptions = {
    clientId:
      "AWqbAvi0Lccqv7SsAZj27F84s8ZhL5YFWDd2m2Ve5Jy6hEjldlRXNkv7v-8p1nFRVmYJ7ASjRhA3dnfq",
    currency: "USD",
  };

  const createOrder = (data: any, actions: any) => {
    console.log(total);
    return actions.order.create({
      purchase_units: [
        {
          amount: {
            value: total,
          },
        },
      ],
    });
  };

  const onApprove = async (data: any, actions: any) => {
    return await actions.order.capture().then(async function (details: any) {
      await dispatch(checkOut());
      dispatch(getCartAsync());
    });
  };
  

  const onError = (err: any) => {
    // Handle errors
    // console.error(err);
  };

  return (
    <div>
      <PayPalScriptProvider options={paypalOptions}>
        <PayPalButtons
          key={total}
          createOrder={createOrder}
          onApprove={onApprove}
          onError={onError}
        />
      </PayPalScriptProvider>
    </div>
  );
};

export default Pay;
