import React from "react";
import { Container, Typography, Link, Grid } from "@mui/material";
import GitHubIcon from "@mui/icons-material/GitHub";
import LinkedInIcon from "@mui/icons-material/LinkedIn";

const Footer = () => {
  return (
    <footer>
      <Container maxWidth="lg" sx={{display: 'block', backgroundColor: '#5187fc', width: '100%', color: 'white'}}>
        <Grid sx={{display: 'flex', justifyContent: 'space-around'}}>
          <Grid item xs={12} md={6}>
            <Typography variant="h6">Your Company Name</Typography>
            <Typography variant="body2">
              A brief description of your company.
            </Typography>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography variant="h6">Connect with us</Typography>
            <Link href="https://github.com/yourusername" target="_blank" rel="noopener noreferrer">
              <GitHubIcon fontSize="large" color="success" />
            </Link>
            <Link href="https://www.linkedin.com/in/yourusername" target="_blank" rel="noopener noreferrer">
              <LinkedInIcon fontSize="large" color="warning" />
            </Link>
          </Grid>
        </Grid>
        <Typography variant="body2" align="center" style={{ marginTop: "1rem", marginBottom: "1rem" }}>
           {new Date().getFullYear()} Your Company Name. All rights reserved.
        </Typography>
      </Container>
    </footer>
  );
};

export default Footer;
