// RegisterForm.tsx
import React, { useState } from "react";
import { Button, TextField, Typography, createTheme } from "@mui/material";
import { useAppDispatch } from "../../app/hooks";
import { registerAsync } from "./loginSlice";

const RegisterForm: React.FC = () => {
  const dispatch = useAppDispatch();
  const [formData, setFormData] = useState({ username: "", password: "" });

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleRegister = () => {
    dispatch(registerAsync(formData));
  };

  const theme = createTheme({
    typography: {
      fontFamily: "Raleway, Arial",
      fontSize: 16
    },
    
  });

  return (
    <div>
      <Typography variant="h5">Register</Typography>
      <TextField
        label="Username"
        name="username"
        value={formData.username}
        onChange={handleInputChange}
        sx={{ margin: "2px" }}

      />
      <TextField
        label="Password"
        name="password"
        type="password"
        value={formData.password}
        onChange={handleInputChange}
        sx={{ margin: "2px" }}

      />
      <br/>
      <Button variant="contained" color="primary" onClick={handleRegister} sx={{margin: '8px'}}>
        Register
      </Button>
    </div>
  );
};

export default RegisterForm;
