import { Link, Outlet } from "react-router-dom";
import React from "react";
import {
  ToggleButton,
  ToggleButtonGroup,
} from "@mui/material";

const Entry = () => {
  const [alignment, setAlignment] = React.useState("login");

  const handleChange = (
    event: React.MouseEvent<HTMLElement>,
    newAlignment: string
  ) => {
    setAlignment(newAlignment);
  };

  return (
    <div>
      <ToggleButtonGroup
        color="primary"
        value={alignment}
        exclusive
        onChange={handleChange}
        aria-label="Platform"
        sx={{ margin: "18px", padding: '0px'}}
      >
        <ToggleButton value="login">
          <Link
            to={"/login"}
            style={{ textDecoration: "none", color: "InfoText" }}
          >
            Login
          </Link>
        </ToggleButton>
        <ToggleButton value="register">
          <Link
            to={"/register"}
            style={{ textDecoration: "none", color: "success"}}
          >
            Register
          </Link>
        </ToggleButton>
      </ToggleButtonGroup>
      <Outlet />
    </div>
  );
};

export default Entry;
