import axios from "axios";

export async function loadData() {
  const myToken = sessionStorage.getItem("access");
  return await axios.get("https://shope-back-3.onrender.com/products/", {
    headers: {
      "Content-Type": "application/json",
      Authorization: "Bearer " + myToken,
    },
  });
}

export async function deleteProd(prodID: Number) {
  const myToken = sessionStorage.getItem("access");
  return await axios.delete(`https://shope-back-3.onrender.com/products/${prodID}`, {
    headers: {
      "Content-Type": "application/json",
      Authorization: "Bearer " + myToken,
    },
  });
}

export async function addProd(formData: {}) {
  const myToken = sessionStorage.getItem("access");
  return await axios.post("https://shope-back-3.onrender.com/upload_image/", formData, {
    headers: {
      Authorization: "Bearer " + myToken,
    },
  });
}

export async function updProd(prodID: Number, formData: {}) {
  const myToken = sessionStorage.getItem("access");
  return await axios.put(`https://shope-back-3.onrender.com/products/${prodID}`, formData, {
    headers: {
      "Content-Type": "application/json",
      Authorization: "Bearer " + myToken,
    },
  });
}

export async function buyProd(prodID: Number) {
  const myToken = sessionStorage.getItem("access");
  return await axios.post(
    `https://shope-back-3.onrender.com/buyProd/${prodID}`,
    {},
    {
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + myToken,
      },
    }
  );
}
